body {
  margin: 0;
  font-family: 'Mitr', sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Mitr', sans-serif;
}
#root {
  background: black;
}
html {
  background: black;
}
nav {
  font-family: 'Saira', sans-serif;
  font-size: 1rem;
  background: black;
}

h1, h2 {
  font-family: 'Saira Stencil One', cursive;
  color: #1af0dc;
  margin: 4rem 2rem;
}

