@keyframes glitch-a{10%,30%,50%,70%,90%{--top: 0;--left: 0}0%{--v-height: 15%}20%{--left: .005}40%{--left: .01;--v-height: 20%;--top: 3}60%{--left: .03;--v-height: 25%;--top: 6}80%{--left: .07;--v-height: 5%;--top: 8}100%{--left: .083;--v-height: 30%;--top: 1}}@-webkit-keyframes glitch-b{10%,30%,50%,70%,90%{--top: 0;--left: 0}0%{--v-height: 15%;--top: 10}20%{--left: -.005}40%{--left: -.01;--v-height: 17%;--top: 3}60%{--left: -.03;--v-height: 35%;--top: 6}80%{--left: -.07;--v-height: 5%;--top: 8}100%{--left: -.083;--v-height: 30%;--top: 1}}@keyframes glitch-b{10%,30%,50%,70%,90%{--top: 0;--left: 0}0%{--v-height: 15%;--top: 10}20%{--left: -.005}40%{--left: -.01;--v-height: 17%;--top: 3}60%{--left: -.03;--v-height: 35%;--top: 6}80%{--left: -.07;--v-height: 5%;--top: 8}100%{--left: -.083;--v-height: 30%;--top: 1}}

.icons{width:4rem;height:4rem;margin:1rem}.iconContainer{background:radial-gradient(circle, rgba(255,255,255,0.748337) 0%, rgba(0,230,255,0.692314) 100%);margin:0rem 1rem 5rem 1rem;display:flex;align-items:center;justify-content:center;width:7rem;border-radius:9rem}.iconSection{display:flex}

.card-project{width:18rem;background:black;border:rgba(0,230,255,0.692314);border-style:solid}.btn{margin:0.5rem}

